<template>
  <div class="activityCenter mt72">
    <!-- <div class=" bf nav">
      <el-menu :default-active="activeIndex" class="el-menu-demo" mode="horizontal" active-text-color="#f25c29" @select="handleSelect">
        <el-menu-item index="1">未完成作业</el-menu-item>
        <el-menu-item index="2">已完成作业</el-menu-item>
        <el-menu-item index="3">作业收藏</el-menu-item>
        <el-menu-item index="4">浏览记录</el-menu-item>
      </el-menu>
    </div>
    <router-view/>-->
    <div class="classList mt72 bf bx">
      <div class="selete">
        <div class="boxLeft">
          <div class="xueke">
            <el-cascader></el-cascader>
          </div>
          <div class="three" style="margin-top:20px">
            <subject />
          </div>
        </div>
      </div>
      <div class="homeWorkList">
        <div class="homeworkBox">
          <img src="../../../static/images/作业列表.png" alt />
        </div>
        <div class="table">
          <table>
            <thead>
              <tr style="background-color:#f8f8f8;">
                <td style="color:#fb8b22;font-size:16px;padding:5px">序号</td>
                <td style="color:#fb8b22;font-size:16px;padding:5px">资源名称</td>
                <td style="color:#fb8b22;font-size:16px;padding:5px">类型</td>
                <td style="color:#fb8b22;font-size:16px;padding:5px">功能</td>
                <td style="color:#fb8b22;font-size:16px;padding:5px">能力范围</td>
                <td style="color:#fb8b22;font-size:16px;padding:5px">来源</td>
              </tr>
            </thead>
            <tbody>
              <tr
                v-for="(item, index) in list"
                :key="index"
                style="margin-bottom:5px"
                class="trHover"
              >
                <td style="padding:10px;font-size:16px">{{ index + 1 }}</td>
                <td style="padding:10px;font-size:16px">
                  <a href="javascript:void(0);">内能</a>
                </td>
                <td style="padding:10px;font-size:16px">课件</td>
                <td style="padding:10px;font-size:16px">预习</td>
                <td style="padding:10px;font-size:16px">基础学习</td>
                <td style="padding:10px;font-size:16px">陈老师</td>
              </tr>
            </tbody>
          </table>
          <el-pagination
            layout="prev, pager, next"
            :total="100"
            class="page"
            style="color: #2ab8b3;"
          ></el-pagination>
        </div>
      </div>
      <div class="timeList">
        <el-calendar v-model="value" id="calendar">
          <template slot="dateCell" slot-scope="{ date, data }">
            <div>
              <div class="calendar-day">
                {{
                data.day
                .split("-")
                .slice(2)
                .join("-")
                }}
              </div>
              <div v-for="item in calendarData">
                <div
                  v-if="
                    item.months.indexOf(data.day.split('-').slice(1)[0]) != -1
                  "
                >
                  <div
                    v-if="
                      item.days.indexOf(
                        data.day
                          .split('-')
                          .slice(2)
                          .join('-')
                      ) != -1
                    "
                  >
                    <el-tooltip class="item" effect="dark" :content="item.things" placement="right">
                      <div class="is-selected">{{ item.things }}</div>
                    </el-tooltip>
                  </div>
                  <div v-else></div>
                </div>
                <div v-else></div>
              </div>
            </div>
          </template>
        </el-calendar>
      </div>
      <div class="workBox">
        <div class="wordAttribute">
          <div class="ListAttribute">
            <span>作业属性:</span>
            <el-button class="attribute" size="small">结构化</el-button>
          </div>
          <div class="ListAttribute">
            <span>作业类型:</span>
            <el-button class="attribute" size="small">结构化</el-button>
          </div>
          <div class="ListAttribute">
            <span>展示类型:</span>
            <el-button class="attribute" size="small">结构化</el-button>
          </div>
          <div class="timeStare">
            <p>{{ 1111 }}S</p>
            <el-button type="primary" class="stare">开始计时</el-button>
          </div>
        </div>

        <div class="homeWork">
          <div class="myimg">
            <img src="../../../static/images/学生作业.png" alt />
          </div>
          <p class="title">XXX作业</p>
          <div class="work">
            <img src="../../../static/images/修改.jpg" alt />
          </div>
          <div class="btns">
            <el-button style="background-color: #ff8a00;color:#fff;border:0">提交作业</el-button>
            <el-button style="background-color: #ff8a00;color:#fff;border:0">保存作业</el-button>
            <el-button style="background-color: #159484;color:#fff;border:0">批给作业</el-button>
            <el-button style="background-color: #ff8a00;color:#fff;border:0">分享</el-button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import subject from "../../components/grade/subject";
import FullCalendar from "./FullCalendar.vue";
import eleCalendar from "ele-calendar";
import "ele-calendar/dist/vue-calendar.css";
export default {
  components: {
    FullCalendar,
    eleCalendar,
    subject
  },
  data() {
    return {
      list: [1, 1, 1, 1, 1],
      period: "", //学段
      sujectList: "",
      liid: -1,
      value: new Date(),
      datedef: [
        { date: "2019-11-27", content: "ccc", cid: null },
        { date: "2019-11-29", content: "cccaaaa", cid: null }
      ],
      prop: "2019-12-28", //对应日期字段名,
      calendarData: [
        { months: ["12"], days: ["02"], things: "未完成" },
        { months: ["12"], days: ["03"], things: "未完成" },
        { months: ["12"], days: ["04"], things: "已完成" },
        { months: ["12"], days: ["05"], things: "已完成" },
        { months: ["12"], days: ["06"], things: "已完成" }
      ]
    };
  },
  created() {
    //  this.querySujectList();
    this.queryUserDetailInfo();
    this.getGrade();
  },
  methods: {
    renderContent(h, parmas) {
      //设置lunarcalendar=true,parmas返回值包含农历
      const loop = data => {
        return data.defvalue.value ? (
          <div>
            <div>{data.defvalue.text}</div>
            <span>备选项</span>
          </div>
        ) : (
          <div>{data.defvalue.text}</div>
        );
      };
      return <div style="min-height:60px;">{loop(parmas)}</div>;
    },
    //获取当前学段
    queryUserDetailInfo() {
      this.$post("/sso/queryUserDetailInfo.do").then(response => {
        //console.log(response)
        if (response.code == "5000") {
          this.period = response.data.period;
        }
      });
    },

    //获取学科列表数据
    getGrade() {
      this.$post("/likework/querySujectList.do").then(res => {
        if (res.code == 5000) {
          let period = "";
          var tyepe = 0;
          if (this.period == "高中") {
            tyepe = 3;
          }
          if (this.period == "初中") {
            tyepe = 2;
          }
          if (this.period == "小学") {
            tyepe = 1;
          }
          period = this.period;
          this.sujectList = res.data[period];
        }
      });
    },

    //点击学科列表事件
    querydSuject(id, type, subjectName, i, $event) {
      this.liid = id;
    }
  }
};
</script>
<style lang="less">
.classList {
  padding: 10px;
  background-color: #f8f8f8;
  .selete {
    height: 1080px;
    float: left;
    display: inline-block;
    width: 16%;
    border: 1px solid #eee;
    text-align: center;
    padding: 5px;
    //  height: 691px;
    // margin-left: 20px;
    background-color: #fff;
    .list {
      font-size: 18px;
      color: #f25c29;
      padding: 3px;
      border-bottom: 1px dashed #f25c29;
    }
    .xueke {
      width: 100%;
      padding: 3px;
    }
    .three {
      padding: 10px 0px;
      border-top: 1px solid #eee;
      width: 100%;
      margin-top: 20px;
      box-sizing: border-box;
    }
  }
  .homeWorkList {
    //  border: 1px solid #eee;
    // padding: 5px 15px;
    background-color: #fff;
    display: inline-block;
    width: 82%;
    margin-left: 10px;
    .homeworkBox {
      height: 76px;
      img {
        margin-top: 15px;
      }
    }
    .table {
      width: 100%;
      //  height: 50%;
      margin: 0 auto;
      text-align: center;
      // border: 1px solid #2ab8b3;
      //  margin-bottom: 15px;
      border-bottom: 10px solid #f8f8f8;
      table {
        width: 100%;
        height: 100%;
        // text-align: center;
        padding: 18px;
        .trHover:hover {
          background-color: #faf1ed;
        }
      }
      .page {
        position: relative;
        margin: 5px;
        right: -318px;
        bottom: 0;
      }
    }
  }

  .calendar-day {
    text-align: center;
    color: #202535;
    line-height: 30px;
    font-size: 12px;
  }
  .is-selected {
    color: #f8a535;
    font-size: 10px;
    margin-top: 5px;
  }
  #calendar
    .el-button-group
    > .el-button:not(:first-child):not(:last-child):after {
    content: "当月";
  }
  .timeList {
    position: relative;
    right: 0%;
    top: 0;
    width: 82%;
    display: inline-block;
    margin-left: 10px;
  }
  .workBox {
    margin-top: 10px;
    display: flex;
    justify-content: space-between;
    width: 100%;
    background-color: #fff;

    .wordAttribute {
      border-radius: 5px;
      border-top: 10px solid #2bb7b3;
      width: 17%;
      padding-top: 20px;
      text-align: center;
      display: inline-block;

      .ListAttribute {
        margin-bottom: 20px;
        .attribute {
          // border: 0;
          //  background-color: #eee;
        }
      }
      .timeStare {
        margin-top: 80px;
        .stare {
          background-color: #2bb7b3;
          border: 0;
          color: #fff;
        }
      }
    }
    .homeWork {
      border-left: 10px solid #f8f8f8;
      display: inline-block;
      padding: 10px;
      background-color: #fff;
      flex: 1;
      .title {
        text-align: center;
        font-size: 22px;
        color: #f25c29;
      }
      .work {
        padding: 15px;
        background-color: #fff;
        width: 80%;
        height: 380px;
        margin: 0 auto;
        border: 1px solid #eee;
        img {
          width: 100%;
          height: 100%;
        }
      }
      .btns {
        float: right;
        margin-right: 97px;
        margin-top: 15px;
      }
    }
  }
}
</style>
<style lang="scss" scope>
@import "@fullcalendar/core/main.css";
@import "@fullcalendar/daygrid/main.css";
@import "@fullcalendar/timegrid/main.css";
</style>



